.content{
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.downloadMessage{
    height: 48px;
    width: 95%;
    display: flex;
    align-items: center;
    background-color: orangered;
    color: white;
    margin: 20px 0px 35px 0px;
    padding: 0 40px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 1.6),0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.downloadText{
    flex: 1;
}
.loadMore{
    cursor: pointer;
    background-color: white;
    color: black;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 1.6),0 2px 10px 0 rgba(0, 0, 0, 0.12);
    border: none;
    font-size: 13px;
    padding: 7px 25px;
    margin-top: -20px;
}

@media (max-width:995px){
    .downloadMessage{
        display: none;
    }
}