@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@300;400&family=Roboto:ital@0;1&display=swap');

.newsImage{
    height: 268px;
    object-fit: cover;
    width: 320px;
    border-radius: 2px;
}
.newsCard{
    display: flex;
    width: 100%;
    border-radius: 5px;
    margin-bottom: 25px;
    padding: 10px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 1.6),0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.newsText{
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.author{
    font-size: 12px;
    font-weight: 300;
    line-height: 22px;
    color: #808290;
}
.lowerText{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
    padding: 10px 0;
}
.source{
    text-decoration: none;
    color: black;
}
.readMore{
    font-size: 12px;
    font-weight: 400;
    padding-top: 10px;
}
.title{
    font-size: 22px;
    line-height: 27px;
    font-family: "Roboto",sans-serif;
}
.muted{
    font-weight: 100;
}
.description{
    font-size: 16px;
    line-height: 22px;
    color: #44444d;
}

@media (max-width:800px){
    .newsCard{
        flex-direction: column;
        width: 100%;
    }
    .newsImage{
        width: 100%;
        height: 100%;
        padding-bottom: 20px;
    }
}